.main-btn {
    width: 185px;
    letter-spacing: 1.5px;
    border-radius: 3px;
    margin-top: 1rem;
  }
  .btn:hover {
    opacity: 0.8;
  }
/* Dashboard v2 */
  .spinner {
    margin: 24px auto;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    border: 6px solid gray;
    border-top: 6px solid gray;
    animation: rotating 1.2s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }
  
  button {
    border: 1px solid black;
    border-radius: 5px;
    background: black;
    height: 48px;
    width: 155px;
    margin-top: 20;
    margin-left: 20;
    color: white;
    font-size: 18px;
  }

  
  @keyframes rotating {
    100% {
      transform: rotate(360deg);
    }
  }